
body {
  margin: 4%;
}

html {
  transition: background-color 1s;
}

#root{
  width: 96%;
  margin: 0 2%;
}

.quotebox {
  height: auto;
  width: 100%;
  border: 1px rgb(145, 126, 126) solid;
  border-radius: 1em;
  text-align: right;
  max-width: 96rem;
}

#quote {
  margin: 0.6em;
  text-align: left;
  font-size: 3.6em;
}

#author {
  margin: 0.4em;
  font-style: italic;
  font-size: 1.6em;
}

.fadeIn {
  opacity: 0;
  display: block;
  animation: slidein 1s ease-out forwards;
}

@keyframes slidein {
  0% {
    opacity: 0;
    transform: translateY(-25px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

#newquote {
  margin: 20px;
  position: relative;
  border-radius: 1em;
  height: 32px;
  width: 120px;
  font-weight: 700;
  font-family: "Segoe UI", "Tahoma", "Geneva", "Verdana", sans-serif;
  color: white;
  font-size: 0.8em;
}